import { ClipBase } from '../clips/base';
import { glsl } from 'typed-glsl';
import * as THREE from 'three';

export function gaussianBlur(clip: ClipBase): THREE.Texture | undefined {
  const { originalWidth, originalHeight } = clip.config;
  const tex = clip.texture;
  const painter = clip.painter;
  let writeBuffer = new THREE.WebGLRenderTarget(originalWidth, originalHeight, {
    depthBuffer: false,
    stencilBuffer: false,
    minFilter: THREE.LinearFilter,
    magFilter: THREE.LinearFilter,
  });
  let readBuffer = new THREE.WebGLRenderTarget(originalWidth, originalHeight, {
    depthBuffer: false,
    stencilBuffer: false,
    minFilter: THREE.LinearFilter,
    magFilter: THREE.LinearFilter,
  });
  const camera = new THREE.OrthographicCamera(originalWidth / - 2, originalWidth / 2, originalHeight / 2, originalHeight / - 2, -10000, 10000);
  const plane = new THREE.PlaneGeometry(originalWidth, originalHeight);
  const material = new THREE.ShaderMaterial({
    uniforms: {
      iResolution: { value: new THREE.Vector3(originalWidth, originalHeight, 0) },
      iChannel0: { value: tex },
      direction: { value: new THREE.Vector2(0, 0) },
    },
    vertexShader: glsl`
      void main() {
        gl_Position = vec4(position, 1.0);
      }
    `,
    fragmentShader: glsl`
      uniform vec3 iResolution;
      uniform sampler2D iChannel0;
      uniform vec2 direction;
      vec4 blur13(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {
        vec4 color = vec4(0.0);
        vec2 off1 = vec2(1.411764705882353) * direction;
        vec2 off2 = vec2(3.2941176470588234) * direction;
        vec2 off3 = vec2(5.176470588235294) * direction;
        color += texture2D(image, uv) * 0.1964825501511404;
        color += texture2D(image, uv + (off1 / resolution)) * 0.2969069646728344;
        color += texture2D(image, uv - (off1 / resolution)) * 0.2969069646728344;
        color += texture2D(image, uv + (off2 / resolution)) * 0.09447039785044732;
        color += texture2D(image, uv - (off2 / resolution)) * 0.09447039785044732;
        color += texture2D(image, uv + (off3 / resolution)) * 0.010381362401148057;
        color += texture2D(image, uv - (off3 / resolution)) * 0.010381362401148057;
        return color;
      }
      vec4 blur9(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {
        vec4 color = vec4(0.0);
        vec2 off1 = vec2(1.3846153846) * direction;
        vec2 off2 = vec2(3.2307692308) * direction;
        color += texture2D(image, uv) * 0.2270270270;
        color += texture2D(image, uv + (off1 / resolution)) * 0.3162162162;
        color += texture2D(image, uv - (off1 / resolution)) * 0.3162162162;
        color += texture2D(image, uv + (off2 / resolution)) * 0.0702702703;
        color += texture2D(image, uv - (off2 / resolution)) * 0.0702702703;
        return color;
      }
      vec4 blur5(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {
        vec4 color = vec4(0.0);
        vec2 off1 = vec2(1.3333333333333333) * direction;
        color += texture2D(image, uv) * 0.29411764705882354;
        color += texture2D(image, uv + (off1 / resolution)) * 0.35294117647058826;
        color += texture2D(image, uv - (off1 / resolution)) * 0.35294117647058826;
        return color; 
      }
      void main() {
        vec2 uv = vec2(gl_FragCoord.xy / iResolution.xy);
        gl_FragColor = blur13(iChannel0, uv, iResolution.xy, direction);
      }
    `,
  });
  const mesh = new THREE.Mesh(plane, material);
  const scene = new THREE.Scene();
  scene.add(mesh);
  const anim = 0.5;
  const iterations = 8;
  for (let i = 0; i < iterations; i++) {
    const radius = (iterations - i - 1) * anim;
    if (i === 0) {
      material.uniforms.iChannel0.value = tex;
    } else {
      material.uniforms.iChannel0.value = readBuffer.texture;
    }
    material.uniforms.direction.value = i % 2 === 0 ? new THREE.Vector2(radius, 0) : new THREE.Vector2(0, radius);
    material.uniformsNeedUpdate = true;
    painter.render(writeBuffer, scene, camera);
    const tempBuffer = writeBuffer;
    writeBuffer = readBuffer;
    readBuffer = tempBuffer;
  }
  return readBuffer.texture;
}
