import React, { useEffect } from 'react';
import { GifClip } from '../../../../../common/types/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { GifFrame } from '../../../../../common/types';

type Props = {
  data: GifClip;
  onLoaded(data: GifClip): void;
};

interface GifProps {
  width: number;
  height: number;
  frames: GifFrame[];
}

function parseGif(buffer: ArrayBuffer) {
  return new Promise<GifProps>((resolve) => {
    const container = document.createElement('div');
    document.body.appendChild(container);
    // @ts-ignore
    const superGif = new SuperGif({
      gif: container,
      draw_while_loading: false,
    });
    superGif.load_raw(new Uint8Array(buffer), () => {
      const { width, height } = superGif.get_canvas() as HTMLCanvasElement;
      container.parentNode?.removeChild(container);
      resolve({
        frames: [...superGif.get_frames()] as GifFrame[],
        width,
        height,
      });
    });
  });
}

const GifCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data?.frames?.length) {
      fetch(data.url).then((response) => {
        return response.arrayBuffer();
      }).then(async (buffer: ArrayBuffer) => {
        const {
          frames,
          width,
          height,
        } = await parseGif(buffer);
        // 转为毫秒
        frames.forEach(frame => {
          frame.delay = frame.delay * 10 || 100;
        });
        const duration = frames.reduce((a, b) => (a + b.delay), 0);
        onLoaded({
          ...data,
          timelineInfo: {
            ...data.timelineInfo,
            duration,
          },
          geometryInfo: {
            ...data.geometryInfo,
            size: {
              width,
              height,
            },
          },
          playRange: {
            start: 0,
            end: duration,
          },
          frames,
          originalWidth: width,
          originalHeight: height,
        });
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <img src={data.url} alt="" draggable="false" />
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default GifCard;
