import { ClipBase } from './base';
import { Shader } from '../painter/shader';
import * as THREE from 'three';
import { VideoClip } from '../../common/types/clip';

export class Video extends ClipBase<VideoClip> {
  public texture?: THREE.VideoTexture;
  private video?: HTMLVideoElement;
  public playbackRate = 1;
  private isPlaying = false;

  public async init() {
    await this.initRender();
    this.updatePlaybackRate();
  }

  private createTexture(): Promise<THREE.VideoTexture> {
    return new Promise(async (resolve, reject) => {
      this.video = document.createElement('video');
      this.video.loop = false;
      this.video.autoplay = false;
      this.video.addEventListener('loadedmetadata', async () => {
        if (this.video) {
          const texture = new THREE.VideoTexture(this.video);
          // @ts-ignore
          await texture.setCurrentTime(this.config.playRange.start);
          texture.onUpdate = this.updateTexture;
          resolve(texture);
        }
      });
      this.video.src = this.config.blobUrl;
    });
  }

  private async initRender() {
    this.texture = await this.createTexture();
    this.material = new THREE.ShaderMaterial({
      uniforms: {
        tex: { value: this.texture },
      },
      vertexShader: Shader.GetVertex(),
      fragmentShader: Shader.GetFragment(),
    });
    this.material.uniformsNeedUpdate = true;
    this.material.transparent = true;
    this.sprite = new THREE.Mesh(this.geometry, this.material);
    this.sprite.position.z = this.config.zIndex;
    this.updatePosition();
    this.updateRotate();
  }

  public play() {
    this.video?.play();
    this.isPlaying = true;
  }

  public pause() {
    this.video?.pause();
    this.isPlaying = false;
  }

  public updatePlaybackRate() {
    const originalDuration = this.config.playRange.end - this.config.playRange.start;
    this.playbackRate = originalDuration / this.config.timelineInfo.duration;
  }

  // 根据player的currentTime校正video的currentTime
  private async correctVideoCurrentTime() {
    const currentTime = this.player.currentTime;
    if (this.video && Math.abs(this.video.playbackRate - this.playbackRate) >= 10e-6) {
      this.video.playbackRate = this.playbackRate;
    }
    const newCurrentTime = this.config.playRange.start + (currentTime - this.config.timelineInfo.start) * this.playbackRate;
    // @ts-ignore
    await this.texture.setCurrentTime(newCurrentTime);
  }

  public async onEnter() {
    // await this.correctVideoCurrentTime();
    if (this.player.isPlaying) {
      this.play();
    }
  }

  public async onLeave() {
    this.pause();
    if (this.texture) {
      // @ts-ignore
      await this.texture.setCurrentTime(this.config.playRange.start);
    }
  }

  public async onUpdate(isManual: boolean) {
    if (isManual) {
      await this.correctVideoCurrentTime();
    }
    this.updateTexture();
  }

  public async destroy() {
    super.destroy();
    if (this.video) {
      this.video = undefined;
    }
    this.texture?.dispose();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}
