import { useSize } from 'ahooks';
import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import Moveable from 'react-moveable';
import { editorContext } from '../..';
import { ClipType } from '../../../common/types/clip';
import { FitType } from '../../../common/types/player';
import { getPlayerContainerBounding } from '../../util';
import { StyledTarget } from './styled';

const renderDirections = ['nw', 'ne', 'sw', 'se'];

const ResizeRotate: React.FC = () => {
  const { activeClip, tracks, setTracks } = useContext(editorContext);
  const [target, setTarget] = useState<HTMLDivElement | null>(null);
  const [targetStyle, setTargetStyle] = useState<React.CSSProperties>();
  const moveableRef = useRef<Moveable>(null);
  const targetRef = useCallback((node: HTMLDivElement) => {
    setTarget(node);
  }, []);

  useEffect(() => {
    if (activeClip) {
      const { left, top } = getPlayerContainerBounding();
      const { size: { width, height }, position: { x, y }, rotate } = activeClip.geometryInfo;
      setTargetStyle({
        width,
        height,
        transform: `translate(${left + x}px, ${top + y}px) rotate(${rotate}deg)`,
      });
    } else {
      setTargetStyle({
        width: 0,
        height: 0,
      });
    }
  }, [activeClip]);

  /** 文本组件需要监听文字内容改变 */
  useEffect(() => {
    if (activeClip?.type === ClipType.TEXT) {
      const { left, top } = getPlayerContainerBounding();
      const { size: { width, height }, position: { x, y }, rotate } = activeClip.geometryInfo;
      setTargetStyle({
        width,
        height,
        transform: `translate(${left + x}px, ${top + y}px) rotate(${rotate}deg)`,
      });
    }
  // @ts-ignore
  }, [activeClip, activeClip?.value]);

  const { width, height } = useSize(target);
  useEffect(() => {
    moveableRef.current?.updateTarget();
  }, [width, height]);

  const handleStart = useCallback(() => {
    if (activeClip && activeClip.fitType !== FitType.CUSTOM) {
      activeClip.fitType = FitType.CUSTOM;
      setTracks([...tracks]);
    }
  }, [activeClip, setTracks, tracks]);

  const handleDrag = useCallback((params) => {
    const { target, delta, transform } = params;
    target.style.transform = transform;
    if (activeClip) {
      Object.assign(activeClip.geometryInfo, {
        position: {
          x: activeClip.geometryInfo.position.x + delta[0],
          y: activeClip.geometryInfo.position.y + delta[1],
        },
      });
    }
  }, [activeClip]);

  const handleResize = useCallback((params) => {
    const { target, width, height, delta, drag } = params;
    const beforeTranslate = drag.beforeTranslate;
    if (activeClip) {
      const { left, top } = getPlayerContainerBounding();
      Object.assign(activeClip.geometryInfo, {
        size: {
          width: width,
          height: height,
        },
      });
      Object.assign(activeClip.geometryInfo, {
        position: {
          x: beforeTranslate[0] - left,
          y: beforeTranslate[1] - top,
        },
      });
    }
    delta[0] && (target!.style.width = `${width}px`);
    delta[1] && (target!.style.height = `${height}px`);
    target!.style.transform = `translate(${beforeTranslate[0]}px, ${beforeTranslate[1]}px) rotate(${activeClip?.geometryInfo.rotate}deg)`;
  }, [activeClip]);

  const handleRotate = useCallback((params) => {
    const { target, transform, absoluteRotate } = params;
    if (activeClip) {
      Object.assign(activeClip.geometryInfo, {
        rotate: absoluteRotate,
      });
    }
    target!.style.transform = transform;
  }, [activeClip]);

  return (
    <>
      <StyledTarget className="resize-rotate" style={targetStyle} ref={targetRef}>
        {/* {
          activeClip?.type === ClipType.TEXT ? <TextEditor clip={activeClip as TextClip} /> : null
        } */}
      </StyledTarget>
      <Moveable
        ref={moveableRef}
        target={target}
        container={null}
        origin={false}
        keepRatio={true}
        renderDirections={renderDirections}

        /* Resize event edges */
        edge={false}

        /* draggable */
        draggable={true}
        // throttleDrag={2}
        onDragStart={handleStart}
        onDrag={handleDrag}

        /* resizable*/
        resizable={true}
        // throttleResize={5}
        onResizeStart={handleStart}
        onResize={handleResize}

        /** rotate */
        rotatable={true}
        // throttleRotate={2}
        onRotateStart={handleStart}
        onRotate={handleRotate}
      />
    </>
  );
};

export default ResizeRotate;
