import React, { useContext, useEffect } from 'react';
import { playerContext } from '../../../..';
import { TRANSITION_DEFAULT_DURATION } from '../../../../../common/configs/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { TransitionClip } from '../../../../../common/types/clip';

type Props = {
  data: TransitionClip;
  onLoaded(data: TransitionClip): void;
};

const TransitionCard: React.FC<Props> = ({ data, onLoaded }) => {
  const { player } = useContext(playerContext);
  useEffect(() => {
    onLoaded({
      ...data,
      timelineInfo: {
        ...data.timelineInfo,
        duration: TRANSITION_DEFAULT_DURATION,
      },
      geometryInfo: {
        ...data.geometryInfo,
        size: {
          width: player?.width || 0,
          height: player?.height || 0,
        }
      }
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [player?.width, player?.height]);
  return (
    <StyledAssetCard>
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default TransitionCard;
