import React from 'react';
import { TextClip } from '../../../../../../../common/types/clip';
import { TRACK_HEIGHT } from '../../../../../../config';

type Props = {
  clip: TextClip;
};

const TextTrack: React.FC<Props> = ({ clip }) => {
  const { value } = clip;

  return (
    <div style={{ width: '100%' }}>
      <div
        style={{
          position: 'absolute',
          left: 20,
          right: 20,
          lineHeight: `${TRACK_HEIGHT}px`,
          color: '#fff',
          overflow: 'hidden',
          whiteSpace: 'nowrap',
          textOverflow: 'ellipsis',
        }}
        title={value}
      >
        {value}
      </div>
    </div>
  );
};

export default TextTrack;