import * as THREE from 'three';
import { getPixelRatio } from '../../common/utils';

export default class Painter {
  private scene: THREE.Scene;
  private camera: THREE.Camera;
  private renderer: THREE.WebGLRenderer;
  private maxAnisotropy: number;

  constructor(width: number, height: number) {
    this.scene = new THREE.Scene();
    this.camera = this.createCamera(0, width, 0, height);
    this.renderer = new THREE.WebGLRenderer({ 
      preserveDrawingBuffer: true,
      antialias: true,
    });
    this.renderer.setPixelRatio(getPixelRatio());
    this.renderer.setSize(width, height);
    this.maxAnisotropy = this.renderer.capabilities.getMaxAnisotropy();
  }

  public createCamera(left: number, right: number, top: number, bottom: number) {
    return new THREE.OrthographicCamera(left, right, top, bottom, -1000, 1000);
  }

  public updateSize(width: number, height: number) {
    this.renderer.setSize(width, height);
    this.camera = this.createCamera(0, width, 0, height);
  }

  public add(sprite: THREE.Mesh, name: string) {
    sprite.name = name;
    this.scene.add(sprite);
  }

  public remove(name: string) {
    const selectedObject = this.scene.getObjectByName(name);
    if (selectedObject) {
      this.scene.remove(selectedObject);
    }
  }

  public getRealSize() {
    const result = new THREE.Vector2();
    this.renderer.getDrawingBufferSize(result);
    return result;
  }

  public getDomElement() {
    return this.renderer.domElement;
  }

  public render(target?: THREE.RenderTarget, specificScene?: THREE.Scene, specificCamera?: THREE.Camera) {
    const { renderer, scene, camera } = this;
    if (target && specificScene && specificCamera) {
      renderer.setRenderTarget(target);
      renderer.render(specificScene, specificCamera);
    } else {
      renderer.setRenderTarget(null); // 输出到canvas
      renderer.render(scene, camera);
    }
  }
}
