import { noEffect } from './noEffect';
import { gray } from './gray';
import { EffectType } from '../../common/types/player';
import { ObjectType } from '../../common/types';
import { gaussianBlur } from './gaussianBlur';
import { ClipBase } from '../clips/base';
import * as THREE from 'three';
import { triangleBlur } from './triangleBlur';

const effectMap: ObjectType<(clip: ClipBase) => THREE.Texture | undefined> = {
  [EffectType.NO_EFFECT]: noEffect,
  [EffectType.GRAY]: gray,
  [EffectType.GAUSSIAN_BLUR]: gaussianBlur,
  [EffectType.TRIANGLE_BLUR]: triangleBlur,
};

export default function effects(type?: EffectType) {
  return effectMap[type || EffectType.NO_EFFECT];
}
