import { Shader } from '../painter/shader';
import transitions from '../transitions';
import { ClipBase } from './base';
import { TransitionClip } from '../../common/types/clip';
import * as THREE from 'three';
import { watch } from '../../common/utils/watch';

export class Transition extends ClipBase<TransitionClip> {
  private fromClip?: ClipBase;
  private toClip?: ClipBase;

  public async init() {
    await this.initRender();
    this.watchChange();
  }

  private watchChange() {
    if (this.fromClip && this.toClip) {
      watch(this.fromClip.config, 'effect', () => {
        this.updateFromClipTexture();
      });
      watch(this.fromClip.config.geometryInfo, 'size', () => {
        this.updateFromClipTexture();
      });
      watch(this.fromClip.config.geometryInfo, 'position', () => {
        this.updateFromClipTexture();
      });
      watch(this.fromClip.config.geometryInfo, 'rotate', () => {
        this.updateFromClipTexture();
      });
      watch(this.toClip.config, 'effect', () => {
        this.updateToClipTexture();
      });
      watch(this.toClip.config.geometryInfo, 'size', () => {
        this.updateToClipTexture();
      });
      watch(this.toClip.config.geometryInfo, 'position', () => {
        this.updateToClipTexture();
      });
      watch(this.toClip.config.geometryInfo, 'rotate', () => {
        this.updateToClipTexture();
      });
    }
  }

  private updateFromClipTexture() {
    if (this.fromClip && this.material) {
      this.fromClip.renderToTexture();
      this.material.uniforms.from.value = this.fromClip.renderTarget.texture;
    }
  }

  private updateToClipTexture() {
    if (this.toClip && this.material) {
      this.toClip.renderToTexture();
      this.material.uniforms.to.value = this.toClip.renderTarget.texture;
    }
  }

  // 由于不知道关联视频或者图片的初始状态，所以需要等到渲染的时候再初始化
  private async initRender() {
    const { player } = this;
    const { fromId, toId, transition } = this.config;
    const fromClip = this.fromClip = player.getClipById(fromId);
    const toClip = this.toClip = player.getClipById(toId);
    if (fromClip && toClip) {
      fromClip.renderToTexture();
      toClip.renderToTexture();
      const { uniforms: transitionUniforms, fragment: transitionFragment } = transitions(transition)(this);
      this.material = new THREE.ShaderMaterial({
        uniforms: {
          from: { value: fromClip.renderTarget.texture },
          to: { value: toClip.renderTarget.texture },
          progress: { value: 0 },
          ...transitionUniforms,
        },
        vertexShader: Shader.GetVertex(),
        fragmentShader: Shader.GetTransitionFragment(transitionFragment),
      });
      this.material.uniformsNeedUpdate = true;
      this.material.transparent = true;
      this.sprite = new THREE.Mesh(this.geometry, this.material);
      this.sprite.position.z = toClip.config.zIndex;
      this.updatePosition();
    }
  }

  public async onEnter() {
    if (this.fromClip && this.toClip) {
      this.fromClip.hide();
      this.toClip.hide();
      this.onUpdate();
    }
  }

  public async onLeave() {
    if (this.fromClip && this.toClip) {
      this.fromClip.show();
      this.toClip.show();
    }
  }

  public async onUpdate() {
    if (this.material?.uniforms) {
      const { start, end } = this.config.timelineInfo;
      const progress = (this.player.currentTime - start) / (end - start);
      this.material.uniforms.progress.value = progress;
    }
  }

  public async destroy() {
    super.destroy();
    this.texture?.dispose();
    this.geometry?.dispose();
    this.material?.dispose();
  }
 }
