import React, { useCallback, useContext } from 'react';
import { playerContext } from '../..';
import ExportVideo from '../../../icons/ExportVideo';
import { exportAsVideo } from '../../../player/util';
import ConfigPanel from '../ConfigPanel';
import { ExportButton, StyledStageHeader } from './styled';

const StageHeader: React.FC = () => {
  const { player } = useContext(playerContext);
  const handleExportVideo = useCallback(() => {
    if (player) {
      exportAsVideo(player);
    }
  }, [player]);
  return (
    <StyledStageHeader>
      <div>
        <ConfigPanel />
      </div>
      <div>未命名</div>
      <ExportButton onClick={handleExportVideo}>
        <ExportVideo />
        导出视频
      </ExportButton>
    </StyledStageHeader>
  );
};

export default StageHeader;
