import React, { useCallback, useContext } from 'react';
import { editorContext } from '../..';
import { Clip, ClipType, ImageClip, TextClip, VideoClip } from '../../../common/types/clip';
import { sleep } from '../../../common/utils';
import ImagePanel from './components/ImagePanel';
import VideoPanel from './components/VideoPanel';
import TextPanel from './components/TextPanel';

function getPanel(data: Clip | undefined, onChange: (data: Partial<Clip>) => void) {
  switch (data?.type) {
    case ClipType.VIDEO:
      return <VideoPanel data={data as VideoClip} onChange={onChange} />;
    case ClipType.IMAGE:
      return <ImagePanel data={data as ImageClip} onChange={onChange} />;
    case ClipType.TEXT:
      return <TextPanel data={data as TextClip} onChange={onChange} />;
    default:
      return null;
  }
}

const ConfigPanel: React.FC = () => {
  const { activeClip, setActiveClip, tracks, setTracks } = useContext(editorContext);
  const handleChange = useCallback(async (data: Partial<Clip>, reactive: boolean = false) => {
    if (activeClip) {
      Object.assign(activeClip, data);
      setTracks([...tracks]);
      if (reactive) {
        const tempClip = activeClip;
        setActiveClip(undefined);
        await sleep(50);
        setActiveClip(tempClip);
      }
    }
  }, [activeClip, setActiveClip, setTracks, tracks]);

  // 防止选中的clip失焦
  const handleClick = useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
  }, []);
  return (
    <div onClick={handleClick}>
      {getPanel(activeClip, handleChange)}
    </div>
  );
};

export default ConfigPanel;
