import { morph } from './morph';
import { dreamy } from './dreamy';
import { fade } from './fade';
import { TransitionType } from '../../common/types/player';
import { hexagonalize } from './hexagonalize';
import { ClipBase } from '../clips/base';
import { ObjectType } from '../../common/types';
import { glitchMemories } from './glitchMemories';

const transitionMap = Object.freeze({
  [TransitionType.MORPH]: morph,
  [TransitionType.DREAMY]: dreamy,
  [TransitionType.FADE]: fade,
  [TransitionType.HEXAGONALIZE]: hexagonalize,
  [TransitionType.GLITCH_MEMORIES]: glitchMemories,
});

export default function transitions(type: TransitionType): (clip: ClipBase) => { uniforms: ObjectType, fragment: string } {
  return transitionMap[type];
}
