import { ClipBase } from '../clips/base';
import { glsl } from 'typed-glsl';
import * as THREE from 'three';
import { Shader } from '../painter/shader';

export function triangleBlur(clip: ClipBase): THREE.Texture | undefined {
  const { originalWidth, originalHeight } = clip.config;
  const radius = 30;
  const painter = clip.painter;
  let writeBuffer = new THREE.WebGLRenderTarget(originalWidth, originalHeight, {
    depthBuffer: false,
    stencilBuffer: false,
    minFilter: THREE.LinearFilter,
    magFilter: THREE.LinearFilter,
  });
  let readBuffer = new THREE.WebGLRenderTarget(originalWidth, originalHeight, {
    depthBuffer: false,
    stencilBuffer: false,
    minFilter: THREE.LinearFilter,
    magFilter: THREE.LinearFilter,
  });
  const camera = new THREE.OrthographicCamera(originalWidth / - 2, originalWidth / 2, originalHeight / 2, originalHeight / - 2, -10000, 10000);
  const plane = new THREE.PlaneGeometry(originalWidth, originalHeight);
  const material = new THREE.ShaderMaterial({
    uniforms: {
      tex: { value: clip.texture },
      delta: { value: new THREE.Vector2(radius / originalWidth, 0) },
    },
    vertexShader: Shader.GetVertex(),
    fragmentShader: glsl`
      uniform sampler2D tex;
      uniform vec2 delta;
      varying vec2 texCoords;
      float random(vec3 scale, float seed) {
        /* use the fragment position for a different seed per-pixel */
        return fract(sin(dot(gl_FragCoord.xyz + seed, scale)) * 43758.5453 + seed);
      }
      void main() {
        vec4 color = vec4(0.0);
        float total = 0.0;
        /* randomize the lookup values to hide the fixed number of samples */
        float offset = random(vec3(12.9898, 78.233, 151.7182), 0.0);
        for (float t = -30.0; t <= 30.0; t++) {
            float percent = (t + offset - 0.5) / 30.0;
            float weight = 1.0 - abs(percent);
            vec4 texColor = texture2D(tex, texCoords + delta * percent);
            /* switch to pre-multiplied alpha to correctly blur transparent images */
            texColor.rgb *= texColor.a;
            color += texColor * weight;
            total += weight;
        }
        gl_FragColor = color / total;
        /* switch back from pre-multiplied alpha */
        gl_FragColor.rgb /= gl_FragColor.a + 0.00001;
      }
    `,
  });
  material.uniformsNeedUpdate = true;
  const mesh = new THREE.Mesh(plane, material);
  const scene = new THREE.Scene();
  scene.add(mesh);
  for (let i = 0; i < 2; i++) {
    if (i === 1) {
      material.uniforms.tex.value = readBuffer.texture;
      material.uniforms.delta.value = new THREE.Vector2(0, radius / originalHeight);
    }
    painter.render(writeBuffer, scene, camera);
    const tempBuffer = writeBuffer;
    writeBuffer = readBuffer;
    readBuffer = tempBuffer;
  }
  return readBuffer.texture;
}
