import { ClipBase } from './base';
import { Shader } from '../painter/shader';
import { ImageClip } from '../../common/types/clip';
import * as THREE from 'three';

export class Image extends ClipBase<ImageClip> {
  public async init() {
    await this.initRender();
  }

  private createTexture(): Promise<THREE.Texture> {
    return new Promise((resolve) => {
      new THREE.TextureLoader().load(this.config.blobUrl, (texture) => {
        resolve(texture);
      });
    });
  }

  private async initRender() {
    this.texture = await this.createTexture();
    this.material = new THREE.ShaderMaterial({
      uniforms: {
        tex: { value: this.texture },
      },
      vertexShader: Shader.GetVertex(),
      fragmentShader: Shader.GetFragment(),
    });
    this.material.uniformsNeedUpdate = true;
    this.material.transparent = true;
    this.sprite = new THREE.Mesh(this.geometry, this.material);
    this.sprite.position.z = this.config.zIndex;
    this.updatePosition();
    this.updateRotate();
  }

  public async destroy() {
    super.destroy();
    this.texture?.dispose();
    this.geometry.dispose();
    this.material?.dispose();
  }
}
