import Player from '..';
import * as THREE from 'three';
import { Clip } from '../../common/types/clip';
import Painter from '../painter';
import { removeWatch, watch } from '../../common/utils/watch';
import effects from '../effects';

export abstract class ClipBase<T extends Clip = Clip> {
  private isAttachToPainter: boolean = false;

  /**
   * 用于控制clip的事件回调
   */
  public isEntered: boolean = false;

  public player: Player;
  public painter: Painter;
  public geometry: THREE.PlaneBufferGeometry;
  public material?: THREE.ShaderMaterial;
  public sprite?: THREE.Mesh;
  public config: T;
  public texture?: THREE.Texture;

  // 单独渲染到纹理使用
  public renderTarget: THREE.WebGLRenderTarget;
  public renderTargetCamera: THREE.Camera;
  public renderTargetScene: THREE.Scene;

  constructor(config: T, player: Player) {
    this.config = config;
    this.player = player;
    this.painter = player.painter;
    const { width: playerWidth, height: playerHeight } = player;

    this.renderTarget = new THREE.WebGLRenderTarget(player.width, player.height, {
      depthBuffer: false,
      stencilBuffer: false,
      minFilter: THREE.LinearFilter,
      magFilter: THREE.LinearFilter,
    });
    this.renderTargetCamera = this.painter.createCamera(0, playerWidth, 0, playerHeight);
    this.renderTargetScene = new THREE.Scene();

    const { width, height } = this.config.geometryInfo.size;
    this.geometry = new THREE.PlaneBufferGeometry(width, height);
    this.setSize();
    this.watch();
  }

  public updateTexture() {
    if (this.material) {
      const effectTex = effects(this.config.effect)(this);
      this.material.uniforms.tex.value = effectTex;
    }
  }

  private setSize() {
    const { width, height } = this.config.geometryInfo.size;
    const vertices = new Float32Array([
      0, 0, 0,
      width, 0, 0,
      0, height, 0,
      width, height, 0,
    ]);
    this.geometry.setAttribute('position', new THREE.BufferAttribute(vertices, 3));
    this.geometry.center();
  }

  public updateRotate() {
    if (this.sprite) {
      this.sprite.rotation.z = this.config.geometryInfo.rotate / 180 * Math.PI;
    }
  }

  public updatePosition() {
    const { x, y } = this.config.geometryInfo.position;
    const { width, height } = this.config.geometryInfo.size;
    if (this.sprite) {
      this.sprite.position.x = x + width / 2;
      this.sprite.position.y = y + height / 2;
    }
  }

  private watch() {
    watch(this.config.geometryInfo, 'rotate', () => {
      this.updateRotate();
    });
    watch(this.config.geometryInfo, 'size', () => {
      this.setSize();
    });
    watch(this.config.geometryInfo, 'position', () => {
      this.updatePosition();
    });
    watch(this.config, 'effect', () => {
      this.updateTexture();
    });
    watch(this.config, 'zIndex', (val: number) => {
      if (this.sprite) {
        this.sprite.position.z = val;
      }
    });
  }

  public abstract init(): Promise<void>;

  public play() {}

  public pause() {}

  public show() {
    if (this.sprite) {
      this.sprite.visible = true;
    }
  }

  public hide() {
    if (this.sprite) {
      this.sprite.visible = false;
    }
  }

  public updatePlaybackRate() {}

  public getOriginalDuration() {
    return this.config.playRange.end - this.config.playRange.start;
  }

  public addToPainter() {
    if (!this.isAttachToPainter && this.sprite && this.config.id) {
      this.isAttachToPainter = true;
      this.player.painter.add(this.sprite, this.config.id);
    }
  }

  public removeFromPainter() {
    if (this.isAttachToPainter && this.config.id) {
      this.isAttachToPainter = false;
      this.player.painter.remove(this.config.id);
    }
  }

  /**
   * 单独渲染到纹理
   */
  public renderToTexture() {
    if (this.sprite) {
      this.renderTargetScene.clear();
      const sprite = this.sprite.clone();
      sprite.visible = true;
      this.renderTargetScene.add(sprite);
      this.painter.render(this.renderTarget, this.renderTargetScene, this.renderTargetCamera);
    }
  }

  /**
   * 首次进入clip时调用，不会继续调用onUpdate
   */
  public async onEnter(isManual: boolean) {}

  /**
   * 离开clip时调用
   */
  public async onLeave(isManual: boolean) {}

  /**
   * 播放器时间改变并且clip在有效范围内调用
   */
  public async onUpdate(isManual: boolean) {}

  /**
   * 销毁函数
   */
  public async destroy() {
    this.renderTargetScene.clear();
    this.renderTarget.dispose();
    removeWatch(this.config);
    this.removeFromPainter();
  }
}
