import { ClipBase } from './base';
import * as THREE from 'three';
import { LottieClip } from '../../common/types/clip';
import { AnimationItem } from 'lottie-web';
import { getPixelRatio, loadLottie, setStyles } from '../../common/utils';
import { Shader } from '../painter/shader';

export class Lottie extends ClipBase<LottieClip> {
  private lottieInstance?: AnimationItem;
  private container: HTMLDivElement = document.createElement('div');

  public async init() {
    setStyles(this.container, {
      width: `${this.config.originalWidth * getPixelRatio()}px`,
      height: `${this.config.originalHeight * getPixelRatio()}px`,
    });
    document.body.appendChild(this.container);
    await this.initLottie();
    this.initRender();
  }

  private async initLottie() {
    this.lottieInstance = await loadLottie(this.container, this.config.animationData);
  }

  private initRender() {
    const canvas = this.container.querySelector('canvas');
    if (this.lottieInstance && canvas) {
      this.texture = new THREE.CanvasTexture(canvas);
      this.material = new THREE.ShaderMaterial({
        uniforms: {
          tex: { value: this.texture },
        },
        vertexShader: Shader.GetVertex(),
        fragmentShader: Shader.GetFragment(),
      });
      this.material.uniformsNeedUpdate = true;
      this.material.transparent = true;
      this.sprite = new THREE.Mesh(this.geometry, this.material);
      this.sprite.position.z = this.config.zIndex;
      this.updatePosition();
      this.updateRotate();
    }
  }

  public async onUpdate() {
    if (this.texture && this.lottieInstance) {
      const { player: { currentTime } } = this;
      this.lottieInstance.goToAndStop(currentTime - this.config.timelineInfo.start);
      this.texture.needsUpdate = true;
      this.updateTexture();
    }
  }

  public async destroy() {
    super.destroy();
    this.lottieInstance?.destroy();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}