import React, { useCallback } from 'react';
import { TextClip } from '../../../../../common/types/clip';
import { EffectType } from '../../../../../common/types/player';
import EffectSelector from '../../../../../common/components/EffectSelector';
import { ConfigPanelIconContainer } from '../../../../../common/styleds';
import TextEditor from '../../../../../common/components/TextEditor';
import { getPixelRatio, getTextClipSize } from '../../../../../common/utils';

type Props = {
  data: TextClip;
  onChange(data: Partial<TextClip>): void;
};

const TextPanel: React.FC<Props> = ({ data, onChange }) => {
  const handleEffectChange = useCallback((value: EffectType) => {
    onChange({
      effect: value,
    });
  }, [onChange]);
  const handleTextChange = useCallback((value: string) => {
    const newClip = {
      ...data,
      value,
    };
    const { width, height } = getTextClipSize(newClip);
    Object.assign(data, {
      originalWidth: width,
      originalHeight: height,
    });
    Object.assign(data.geometryInfo, {
      size: {
        width: width / getPixelRatio(),
        height: height / getPixelRatio(),
      },
    });
    Object.assign(data, {
      value,
    });
    onChange(data);
  }, [onChange, data]);
  return (
    <ConfigPanelIconContainer>
      <EffectSelector
        value={data.effect}
        onChange={handleEffectChange}
      />
      <TextEditor
        text={data.value}
        onChange={handleTextChange}
      />
    </ConfigPanelIconContainer>
  );
};

export default TextPanel;
