import { TextClip } from '../../common/types/clip';
import { ClipBase } from './base';
import * as THREE from 'three';
import { svgToCanvas, textClipToSvg } from '../../common/utils';
import { Shader } from '../painter/shader';
import { watch } from '../../common/utils/watch';

export class Text extends ClipBase<TextClip> {
  private canvas: HTMLCanvasElement = document.createElement('canvas');
  private context?: CanvasRenderingContext2D | null;

  public texture?: THREE.CanvasTexture;

  public async init() {
    await this.initTexture();
    this.initRender();
    this.watchChange();
  }

  private watchChange() {
    watch(this.config, 'value', async () => {
      await this.updateCanvas();
      this.texture!.needsUpdate = true;
    });
  }

  private async updateCanvas() {
    const { originalWidth, originalHeight } = this.config;
    this.canvas.width = originalWidth;
    this.canvas.height = originalHeight;
    this.context?.clearRect(0, 0, originalWidth, originalHeight);
    const svg = textClipToSvg(this.config);
    await svgToCanvas(svg, this.canvas, this.context);
  }

  private async initTexture() {
    this.context = this.canvas.getContext('2d');
    await this.updateCanvas();
    this.texture = new THREE.CanvasTexture(this.canvas);
  }

  private initRender() {
    this.material = new THREE.ShaderMaterial({
      uniforms: {
        tex: { value: this.texture },
      },
      vertexShader: Shader.GetVertex(),
      fragmentShader: Shader.GetFragment(),
    });
    this.material.transparent = true;
    this.sprite = new THREE.Mesh(this.geometry, this.material);
    this.sprite.position.z = this.config.zIndex;
    this.updatePosition();
    this.updateRotate();
  }

  public async destroy() {
    super.destroy();
    this.texture?.dispose();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}
