import React, { useCallback } from 'react';
import { GeometryInfo, VideoClip } from '../../../../../common/types/clip';
import { EffectType } from '../../../../../common/types/player';
import EffectSelector from '../../../../../common/components/EffectSelector';
import FitSelector from '../../../../../common/components/FitSelector';
import { ConfigPanelIconContainer } from '../../../../../common/styleds';

type Props = {
  data: VideoClip;
  onChange(data: Partial<VideoClip>, reactive?: boolean): void;
};

const VideoPanel: React.FC<Props> = ({ data, onChange }) => {
  const handleEffectChange = useCallback((value: EffectType) => {
    onChange({
      effect: value,
    });
  }, [onChange]);
  const handleFitChange = useCallback((value: Partial<GeometryInfo>) => {
    Object.assign(data.geometryInfo, value);
    onChange(data, true);
  }, [data, onChange]);
  return (
    <ConfigPanelIconContainer>
      <EffectSelector
        value={data.effect}
        onChange={handleEffectChange}
      />
      <FitSelector
        clip={data}
        onChange={handleFitChange}
      />
    </ConfigPanelIconContainer>
  );
};

export default VideoPanel;
